@charset 'utf-8';

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 28px;
  margin: 14px 0 12px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 8px 0 6px;
  margin-left: 0;
  // color: black;
  font-size: 12px;
  font-weight: normal;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
  border: 1px solid transparent;
  border-radius: 5px;
  transition: border-color 250ms var(--timing-function);
}

.tui-pagination .tui-page-btn:hover,
.tui-page-btn:focus {
  border-color: #ff6b08;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #ff6b08;
  border-color: #ff6b08;
  cursor: default;
  width: 40px;
  height: 40px;

  background: #ff6b08;
  border-radius: 5px;
  // transition: background-color 250ms var(--timing-function);
}

